
.toc {
  position: absolute;
  margin: auto;
  max-width: 44em;
  z-index: 4;
}
.toc .hd > h2 {
  text-align: center;
  margin-bottom: 1.875em;
}
.toc .bd {
  overflow: scroll;
}
.toc li > a {
  display: block;
  color: #666;
  height: 2.375em;
  line-height: 2.375em;
  padding: 0 3em 0 1em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.toc li > span {
  float: right;
  margin-top: -2em;
  color: #777;
}


