
body {
  font: 16px 'Helvetica Neue', Helvetica, 'Lucida Grande', 'Luxi Sans', Arial, 'Hiragino Sans GB', STHeiti, 'Microsoft YaHei', 'Wenquanyi Micro Hei', 'WenQuanYi Micro Hei Mono', 'WenQuanYi Zen Hei', 'WenQuanYi Zen Hei Mono', LiGothicMed;
  overflow-x: hidden;
  background: #e5e4db;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote {
  word-wrap: break-word;
}
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
form,
fieldset,
input,
button,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
ol,
ul {
  list-style: none;
}
.article {
  position: relative;
  margin: auto;
  max-width: 44em;
  overflow: hidden;
}
.inner {
  position: absolute;
}
.layout-vertical .inner {
  position: relative;
}


