
.aside {
  position: fixed;
  z-index: 50;
  width: 2.5em;
  top: 0;
  left: 0;
  height: 100%;
  background: #e5e4db;
}
@media screen and (max-width: 599px) {
.aside {
    display: none;
}
}
.aside .panel {
  width: 1.5em;
  margin: 0.625em auto;
}
.aside .panel li {
  padding: 0;
  text-align: center;
  position: relative;
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #cecdc5;
}


