
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  transform: translate(100%, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  transform: translate(-100%, 0);
}
.page {
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  max-width: 34em;
  float: left;
  overflow: hidden;
  padding: 5em 5em 3em;
  margin: 0;
  border: none;
  background: #f6f4ec;
}
@media screen and (max-width: 599px) {
.page {
    padding: 2.5em 1.5em;
}
}
.page .page-wrapper {
  margin-top: 6em;
}
.page .page-wrapper .info-list {
  margin: 3em 0;
}
.page .page-wrapper h2 {
  padding-top: 3em;
  padding-bottom: 1em;
}
.page .bd {
  overflow: hidden;
}
.page .bd .content h1,
.page .bd .content h2,
.page .bd .content h3,
.page .bd .content h4,
.page .bd .content h5,
.page .bd .content h6 {
  font-size: 100%;
  font-weight: normal;
}
.page .bd .content h1 {
  font-size: 2em;
  font-weight: bold;
  line-height: 3em;
}
.page .bd .content h2 {
  font-size: 1.25em;
  font-weight: bold;
}
.page .bd .content hr {
  margin: 11px 0;
}
.page .bd .content p,
.page .bd .content pre {
  text-indent: 2em;
  line-height: 2;
  min-height: 2em;
  text-align: justify;
  text-justify: inter-ideograph;
  -ms-text-justify: inter-ideograph;
  text-autospace: ideograph-alpha;
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
  white-space: pre-line;
  line-height: 1.5em;
  min-height: 1.5em;
  font-weight: inherit;
  font-style: inherit;
}
.page .bd .content p img,
.page .bd .content pre img {
  margin-left: -2em;
  width: 100%;
}
.page .hd,
.page .ft {
  display: none;
}
.page:hover .tip {
  display: block;
}
.page .tip {
  display: none;
}
.page .tip a {
  display: block;
  line-height: 40px;
  position: absolute;
  top: 50%;
  margin-top: -20px;
  width: 40px;
  height: 40px;
  background-color: #000;
  opacity: 0.3;
  overflow: hidden;
  z-index: 4;
}
.page .tip .prevBtn {
  left: 5px;
}
.page .tip .nextBtn {
  right: 5px;
}
.page-loading {
  text-align: center;
}
.toc .hd {
  display: initial;
}
.page-reader .hd,
.page-reader .ft {
  display: initial;
}
.page-reader .hd {
  position: absolute;
  top: 2em;
  padding-bottom: 8px;
  color: #aaa;
  overflow: hidden;
  border-bottom: 1px solid #e9eadf;
}
@media screen and (max-width: 599px) {
.page-reader .hd {
    top: 0.7em;
    height: 1rem;
}
}
.page-reader .hd h3 {
  color: #855b5b;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.page-reader .ft {
  position: absolute;
  color: #999;
  font: 0.75em Georgia;
  bottom: 2em;
}
@media screen and (max-width: 599px) {
.page-reader .ft {
    text-align: center;
    bottom: 1.2em;
}
}
.layout-vertical .article {
  height: auto !important;
}
.layout-vertical .page-reader {
  float: none;
  height: auto !important;
  padding-top: 0;
  padding-bottom: 0;
}
.layout-vertical .page-reader .hd {
  display: none;
}
.layout-vertical .page-reader .ft {
  left: 0em;
  bottom: auto;
  font-size: 2em;
  text-align: right;
}
.layout-vertical .page-reader .ft span {
  display: none;
}


