
.spinner {
  transition: opacity 0.15s ease;
  animation: rotator 1.4s ease-in-out infinite;
}
.spinner.v-enter,
.spinner.v-leave-active {
  opacity: 0;
}
.spinner.v-enter-active,
.spinner.v-leave {
  opacity: 1;
}
.spinner .path {
  stroke: #935352;
  stroke-dashoffset: 2;
  stroke-dasharray: 10;
  transform-origin: center;
}
@keyframes rotator {
from {
    transform: scale(0.5) rotate(0deg);
}
to {
    transform: scale(0.5) rotate(360deg);
}
}


